footer{
    background-color: var(--color-primary);
    padding: 3rem 0 ;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: var(--color-bg);
}
.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 3rem;
}
.footer__socials a{
    background-color: var(--color-bg);
    color: var(--color-white);
    padding: 0.6rem;
    border-radius: 0.5rem;
    display: flex;
    border: 1px solid transparent;
    font-size: 18px;
}
.footer__socials a:hover{
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer__copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}



/*  Media queries (Medium devices) */

@media screen and (max-width: 600px) {
  .permalinks{
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer__socials{
    margin-bottom: 2.6rem;
  }
}